'use client'

import { useState, useEffect } from 'react'
import axios from 'axios'

const LottoPage = () => {
  const [lottoData, setLottoData] = useState<any>(null)
  const [loading, setLoading] = useState<boolean>(true)
  const [error, setError] = useState<string | null>(null)

  // Fetch lotto data from Strapi API using axios
  useEffect(() => {
    const fetchLottoData = async () => {
      try {
        const response = await axios.get('http://localhost:1337/api/lotto6s', {
          params: {
            fields: 'id,lottonumber',  // Passing the query parameter to fetch specific fields
          }
        })
        // Axios response comes in the form { data: { data: [...] } }
        setLottoData(response.data.data)
      } catch (err) {
        setError('Error fetching lotto data.')
        console.error(err)
      } finally {
        setLoading(false)
      }
    }

    fetchLottoData()
  }, [])  // Empty dependency array ensures it runs once on mount

  if (loading) {
    return <div>Loading lotto data...</div>
  }

  if (error) {
    return <div>{error}</div>
  }

  return (
    <div className="mx-auto max-w-7xl px-4 py-6 sm:px-6 lg:px-8">
      <h1 className="text-2xl font-semibold text-gray-900">Lotto Data</h1>
      {lottoData && lottoData.length > 0 ? (
        <div className="mt-6">
          <h2 className="text-xl font-semibold text-gray-900">Lotto Results</h2>
          <ul className="mt-4 space-y-2">
            {lottoData.map((lotto: { id: number, lottonumber: string }) => (
              <li key={lotto.id} className="text-lg text-gray-700">
                <span className="font-semibold">ID:</span> {lotto.id} <br />
                <span className="font-semibold">Lotto Number:</span> {lotto.lottonumber}
              </li>
            ))}
          </ul>
        </div>
      ) : (
        <p>No lotto data available.</p>
      )}
    </div>
  )
}

export default LottoPage
